body{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: rgb(126, 144, 158);
}
.container{
  margin-top: 10px;
  width: 270px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
}
img{
  width: 250px;
  height: 250px;
  margin-top: 10px;
  border-radius: 10px;
}
h3{
  text-align: center;
  margin: 20px 25px;
}
p{
  text-align: center;
  margin: 0px 25px 30px 25px;
  color: rgb(124, 141, 155);
}